@import url(https://fonts.cdnfonts.com/css/visitor);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes animate-idle{
    from{
        background-position-x: 0px;
        background-image: url(/assets/girl_idle.png);
        -webkit-animation-timing-function: steps(8);
                animation-timing-function: steps(8);
        width: 108px;
    }
    to{
        background-position-x: -865px;
        background-image: url(/assets/girl_idle.png);
        -webkit-animation-timing-function: steps(8);
                animation-timing-function: steps(8);
        width: 108px
    }
}


@keyframes animate-idle{
    from{
        background-position-x: 0px;
        background-image: url(/assets/girl_idle.png);
        -webkit-animation-timing-function: steps(8);
                animation-timing-function: steps(8);
        width: 108px;
    }
    to{
        background-position-x: -865px;
        background-image: url(/assets/girl_idle.png);
        -webkit-animation-timing-function: steps(8);
                animation-timing-function: steps(8);
        width: 108px
    }
}


@-webkit-keyframes animate-running{
    from{
        background-position-x: 0px;
        background-image: url(/assets/girl_run.png);
        -webkit-animation-timing-function: steps(6);
                animation-timing-function: steps(6);
        width: 87px;
    }to{
        background-position-x: -522px;
        background-image: url(/assets/girl_run.png);
        -webkit-animation-timing-function: steps(6);
                animation-timing-function: steps(6);
        width: 87px;
    }
}


@keyframes animate-running{
    from{
        background-position-x: 0px;
        background-image: url(/assets/girl_run.png);
        -webkit-animation-timing-function: steps(6);
