@-webkit-keyframes moving-ground{
    from{
        transform: translateX(0);
    }
    to{
        transform: translateX(-100vw);
    }
}

@keyframes moving-ground{
    from{
        transform: translateX(0);
    }
    to{
        transform: translateX(-100vw);
    }
}

.ground{
    background-image: url('/assets/ground.png');
    background-repeat: repeat-x;
    width: 100vw;
    height: 8vh;
    bottom: 0;
    background-size: contain;
    -webkit-animation: moving-ground 8s linear forwards;
            animation: moving-ground 8s linear forwards;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@media only screen and (max-width: 500px){
    .ground{
        height: 6vh;
    }
}
@-webkit-keyframes moving-parallax{
    from{
        transform: translateX(0);
    }
    to{
        transform: translateX(-100vw);
    }
}

@keyframes moving-parallax{
    from{
        transform: translateX(0);
    }
    to{
        transform: translateX(-100vw);
    }
}

.parallax{
    background-repeat: repeat-x;
    min-height: -webkit-fill-available;
    width: 100vw;
    background-size: contain;
    position: absolute;
    bottom: 0;
    -webkit-animation-name: moving-parallax;
            animation-name: moving-parallax;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    z-index: -1;
    /* border: 2px solid red; */
}
@-webkit-keyframes top-to-down{
    0%{
        margin-top: -60vh;
    }
    80%{
        margin-top: -20vh;
    }
    90%{
        margin-top: -24vh;
    }
    100%{
        margin-top: -20vh;
    }
}

@keyframes top-to-down{
    0%{
        margin-top: -60vh;
    }
    80%{
        margin-top: -20vh;
    }
    90%{
        margin-top: -24vh;
    }
    100%{
        margin-top: -20vh;
    }
}

@-webkit-keyframes appear{
    from{
        opacity: 0%;
    }
    to{
        opacity: 100%;
    }
}

@keyframes appear{
    from{
        opacity: 0%;
    }
    to{
        opacity: 100%;
    }
}

.home-panel{
    background-image: url("/assets/menu_panel.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    height: 90vh;
    margin-top: -20vh;

    padding: 6vh 6vw;
    -webkit-animation: top-to-down 0.8s ease-out forwards;
            animation: top-to-down 0.8s ease-out forwards;
}

.not-found-panel{
    padding-bottom: 8vh;
}



.start-btn{
    background-color: #007C75;
    color: white;
    opacity: 0%;
    -webkit-animation: appear 0.8s ease-in forwards;
            animation: appear 0.8s ease-in forwards;
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
}

